<template>
    <div id='gymbranch' class='leakind'>
        <div class='top'>
          <span class='fundInfo' @click='goback()'>会员卡管理</span>
          <img src='../../assets/images/icon_nav_arrow.png'/>
          <span class='branch'>{{text}}</span>
        </div>
        <el-form ref="form" :model="form" label-width="80px" :rules='rules'>
            <div class='info'>
                <div class='left'>
                  <div class='search'>
                    <el-form-item label="绑定会员：" prop='personMobile' tabindex = "-1" :class="isActive === 1 ? 'border-active formNew search' : form.personName ? 'formNew search person' : 'formNew search'">
                      <el-input
                          class="inline-input"
                          v-model="form.personMobile"
                          placeholder="请输入手机号/姓名/邮箱"
                          @focus="inDiv(1)"
                          @blur="outDiv(1)"
                      ></el-input>
                      <div class='sea' @click="querySearch(form.personMobile, 'personMobile')">搜索</div>
                    </el-form-item>
                    <ul class="searchList" v-if="peronList.length > 0 && seachType === 'personMobile'">
                        <li class="searchItem" v-for="(item,index) in peronList" :key="index" @click="searchClick(item)">
                          {{item.personName || item.phoneNum || item.email}}
                        </li>
                    </ul>
                  </div>
                  <div class='per' v-if="form.personName && !needRegister">
                    <span>用户：</span> <span>{{form.personName}}({{form.newpersonMobile}})</span>
                  </div>
                  <div class='per' v-if="!form.personName && needRegister">
                    <div class='noperson'><img src='../../assets/images/icon_warnig.png'/>用户不存在</div>
                    <div class='regist'>请帮助用户<span @click="registPeron('needRegister')">注册账号</span></div>
                  </div>
                  <div class='per' v-if="!form.personName && email">
                    <div class='noperson'><img src='../../assets/images/icon_warnig.png'/>用户不存在，请让用户在APP注册</div>
                  </div>
                  <div class='search'>
                    <el-form-item label="联名会员：" prop='combinMobile' tabindex = "-1" :class="isActive === 11 ? 'border-active  search' : form.combinName ? 'search person' : 'search'">
                      <el-input
                          class="inline-input"
                          v-model="form.combinMobile"
                          placeholder="请输入手机号/姓名/邮箱"
                          @focus="inDiv(11)"
                          @blur="outDiv(11)"
                      ></el-input>
                      <div class='sea' @click="querySearch(form.combinMobile, 'combinMobile')">搜索</div>
                    </el-form-item>
                    <ul class="searchList" v-if="peronList.length > 0 && seachType === 'combinMobile'">
                          <li class="searchItem" v-for="(item,index) in peronList" :key="index" @click="searchClick(item)">
                            {{item.personName || item.phoneNum || item.email}}
                          </li>
                      </ul>
                  </div>
                  <div class='per' v-if="form.combinName && !contactRegister">
                    <span>用户：</span> <span>{{form.combinName}}({{form.newcombinMobile}})</span>
                  </div>
                  <div class='per' v-if="!form.combinName && contactRegister">
                    <div class='noperson'><img src='../../assets/images/icon_warnig.png'/>用户不存在</div>
                    <div class='regist'>请帮助用户<span @click="registPeron('contactRegister')">注册账号</span></div>
                  </div>
                  <div class='per' v-if="!form.combinName && contactemail">
                    <div class='noperson'><img src='../../assets/images/icon_warnig.png'/>用户不存在，请让用户在APP注册</div>
                  </div>
                  <el-form-item label="所属门店：" class ='formNew pic'  prop='openCardByBranchId' tabindex = "-1" :class="isActive === 2 ? 'border-active' : ''">
                      <el-select v-model="form.openCardByBranchId" placeholder="请选择" clearable @focus="inDiv(2)" @blur="outDiv(2)" @change="selBranch">
                        <el-option
                        v-for="(item, index) in branchlist"
                        :key="index"
                        :label="item.name"
                        :value="item.id"
                        :class="{whiteColor: index % 2 !== 0 ? true : false}"
                        >
                        {{item.name}}
                        <img src='../../assets/images/icon_right.png'/>
                        </el-option>
                     </el-select>
                  </el-form-item>
                  <el-form-item label="会员卡类型：" class='card formNew' prop="vipCardType">
                      <el-radio @change='cardType' v-model="form.vipCardType" label="PREPAID" border :class="{iconfont: form.vipCardType === 'PREPAID' ? true: false, iconiconselect: form.vipCardType === 'PREPAID' ? true: false}">储值卡</el-radio>
                      <el-radio @change='cardType' v-model="form.vipCardType" label="TIMES" border :class="{iconfont: form.vipCardType === 'TIMES' ? true: false, iconiconselect: form.vipCardType === 'TIMES' ? true: false}">次卡</el-radio>
                      <el-radio @change='cardType' v-model="form.vipCardType" label="DATELIMIT" border :class="{iconfont: form.vipCardType === 'DATELIMIT' ? true: false, iconiconselect: form.vipCardType === 'DATELIMIT' ? true: false}">会籍卡</el-radio>
                      <el-radio @change='cardType' v-model="form.vipCardType" label="PACKAGE" border :class="{iconfont: form.vipCardType === 'PACKAGE' ? true: false, iconiconselect: form.vipCardType === 'PACKAGE' ? true: false}">期限卡</el-radio>
                  </el-form-item>
                  <!-- <el-form-item label="时长（分钟）：" class='formNew' prop='time'>
                      <el-input v-model.number="form.time" class='inputNew' placeholder="请输入时长" :maxlength="5"></el-input>
                  </el-form-item> -->
                  <el-form-item label="会员卡种类：" class ='formNew' prop='cardTypeInfoCode' tabindex = "-1" :class="isActive === 3 ? 'border-active' : ''">
                    <el-select v-model="form.cardTypeInfoCode" placeholder="请选择" clearable  @focus="inDiv(3)" @blur="outDiv(3)" ref='cardTypeInfoCode'>
                        <el-option
                        v-for="(item, index) in cardTypelist"
                        :key="index"
                        :label="item.name"
                        :value="item.code"
                        :class="{whiteColor: index % 2 !== 0 ? true : false}">
                        {{item.name}}
                        <img src='../../assets/images/icon_right.png'/>
                        </el-option>
                     </el-select>
                  </el-form-item>
                  <el-form-item :label="formText" class ='formNew' prop='amount' tabindex = "-1" :class="isActive === 4 ? 'border-active' : ''">
                    <el-input v-model="form.amount" class='inputNew' placeholder="请输入" @focus="inDiv(4)" @blur="outDiv(4)" :maxlength="5"></el-input>
                  </el-form-item>
                  <el-form-item label="实体卡号：" tabindex = "-1" :class="isActive === 15 ? 'border-active' : ''">
                    <el-input v-model.number="form.physicalCardNo" class='inputNew'  placeholder="请输入" @focus="inDiv(15)" @blur="outDiv(11)"></el-input>
                  </el-form-item>
                  <el-form-item label="实收（元）：" class ='formNew' prop='actualPay' tabindex = "-1" :class="isActive === 5 ? 'border-active' : ''">
                    <el-input v-model.number="form.actualPay" class='inputNew' maxlength="8" placeholder="请输入" @focus="inDiv(5)" @blur="outDiv(5)"></el-input>
                  </el-form-item>
                  <div class='timePer' v-if="form.vipCardType === 'TIMES'">充值单价：{{timePer | fundFormat(1)}}元/次</div>
                  <el-form-item label="设置有效期(天)：" v-if="form.vipCardType !== 'DATELIMIT' && form.vipCardType !== 'PACKAGE'" tabindex = "-1" :class="isActive === 6 ? 'border-active' : ''">
                    <el-input v-model="form.dayLimit" class='inputNew' maxlength="5" placeholder="请输入"  @focus="inDiv(6)" @blur="outDiv(6)"></el-input>
                  </el-form-item>
                  <div class='tips' v-if="form.vipCardType !== 'DATELIMIT' && form.vipCardType !== 'PACKAGE'">注：（不填则有效期为"无期限"）</div>
                  <el-form-item label="开始时间：" tabindex = "-1" :class="isActive === 7 ? 'border-active' : 'formNew'" prop="startTime">
                    <el-date-picker
                        v-model="form.startTime"
                        type="date"
                        placeholder="选择日期"
                        class='inputNew'
                        @focus="inDiv(7)" 
                        @blur="outDiv(7)"
                    >
                    </el-date-picker>
                  </el-form-item>
                  <div v-if='rechargeMoney'>
                    <div class='text'>充值后余额</div>
                    <div class='balance'>￥{{form.amount | isBalance}}元</div>
                  </div>
                  <div v-if='rechargeTime'>
                    <div class='text'>充值后次数</div>
                    <div class='balance'>{{form.amount | isBalance}}次</div>
                  </div>
                  <div>
                    <div class='text' v-if='deadline'>充值后有效期</div>
                    <div class='balance' v-if="deadline && deadlineDate !== ''">{{deadlineDate | timeformer}}</div>
                    <div class='balance dead' v-if="deadline && deadlineDate === '' && form.vipCardType !== 'DATELIMIT' && form.vipCardType !== 'PACKAGE'">无期限</div>
                  </div>
                </div>
                <div class='right'>
                  <el-form-item label="支付方式：" class='formNew' prop='paymentMode' tabindex = "-1" :class="isActive === 8 ? 'border-active' : ''">
                    <el-select v-model="form.paymentMode " placeholder="请选择" clearable  @focus="inDiv(8)" @blur="outDiv(8)">
                        <el-option
                        v-for="(item, index) in paylist"
                        :key="index"
                        :label="item.name"
                        :value="item.value"
                        :class="{whiteColor: index % 2 !== 0 ? true : false}">
                        {{item.name}}
                        <img src='../../assets/images/icon_right.png'/>
                        </el-option>
                     </el-select>
                  </el-form-item>
                  <el-form-item label="销售：" class='formNew' prop='salesperson' tabindex = "-1" :class="isActive === 9 ? 'border-active' : ''">
                    <el-select v-model="form.salesperson " placeholder="请选择" clearable @focus="inDiv(9)" @blur="outDiv(9)">
                        <el-option
                        v-for="(item, index) in salelist"
                        :key="index"
                        :label="item.name"
                        :value="item.id"
                        :class="{whiteColor: index % 2 !== 0 ? true : false}">
                        {{item.name}}
                        <img src='../../assets/images/icon_right.png'/>
                        </el-option>
                     </el-select>
                  </el-form-item>
                  <el-form-item label="备注：" class='TextArea' tabindex = "-1" :class="isActive === 10 ? 'border-active' : ''">
                      <el-input v-model="form.note" class='inputNew'  type="textarea" placeholder="请输入" @focus="inDiv(10)" @blur="outDiv(10)" :maxlength="255"></el-input>
                  </el-form-item>
                </div>
            </div>
            <div class='btn'>
                <el-form-item>
                   <el-button type="primary" @click='goback()'>取消</el-button>
                   <el-button @click="submit('form')">确定</el-button>
                </el-form-item>
            </div>
        </el-form>
        <!-- <div class='btn'>
            <div class='submit' @click="submit('form')">提交</div>
        </div> -->
        <register :dialogFormVisible="register" v-on:close ="close" v-on:login="login"></register>
    </div>
</template>
<script>
import {getallbranch, getcardtype, getbranchcoach, addcardvip, getpercard, queryMember, getStartBranch, getAllsale} from '../../services/service.js'
import {isApiResponseOk, getSessionStorage} from '../../utils/com.js'
import { setTimeout } from 'timers';
import register from '../../components/register'
export default {
  data () {
    return {
      form: {
        cardTypeInfoCode: '',
        note: '',
        openCardByBranchId: '',
        salesperson: '',
        startTime: '',
        paymentMode: '',
        personName: '',
        combinName: '',
        amount: '', // 充值金额
        actualPay: '', // 实收价格
        dayLimit: '', // 有效天数
      },
      deadline: false, // 充值有效期
      // pickerOptions1: {
      //   disabledDate(time) {
      //     return time.getTime() < Date.now() - 8.64e7;
      //   }
      // },
      rules: {
        openCardByBranchId: [{required: true, message: '门店不能为空', trigger: 'change'}],
        personName: [{required: true, message: '会员姓名不能为空', trigger: 'change'}],
        vipCardType: [{required: true, message: '会员卡类型不能为空', trigger: 'blur'}],
        cardTypeInfoCode: [{required: true, message: '会员卡种类不能为空', trigger: 'change'}],
        amount: [
          {required: true, message: '充值金额不能为空', trigger: 'blur'}
        ],
        actualPay: [
          {required: true, message: '充值金额不能为空', trigger: 'blur'},
          {type: 'number', message: '金额必须为数字类型'}
        ],
        startTime: [{required: true, message: '开始时间不能为空', trigger: 'blur'}],
        paymentMode: [{required: true, message: '支付方式不能为空', trigger: 'change'}],
        salesperson: [{required: true, message: '销售不能为空', trigger: 'change'}]
      },
      modify: false,
      text: '添加会员卡',
      tags: [
        {name: '标签一', type: ''},
        {name: '标签二', type: 'success'},
        {name: '标签三', type: 'info'},
        { name: '标签四', type: 'warning' },
        { name: '标签五', type: 'danger' }
      ],
      actionUrl: 'https://mainapi.com/gymmanage/image/upload',
      fileList: [],
      isShow: true,
      branchlist: [
      ], // 门店列表
      salelist: [], // 销售列表
      paylist: [
        {name: '微信', value: 'WXPAY'},
        {name: '支付宝', value: 'ALIPAY'},
        {name: '现金', value: 'CASH'},
        {name: '刷卡', value: 'SLOTCARD'},
        {name: '转账', value: 'TRANSFER'}
      ], // 支付方式列表
      cardTypelist: [], // 会员卡种类列表
      radio7: '',
      branch: '',
      listdata: [
        { "personId": "三全鲜食（北新泾店）", "personName": "长宁区新渔路144号" },
        { "personId": "Hot honey 首尔炸鸡（仙霞路）", "personName": "上海市长宁区淞虹路661号" },
        { "personId": "新旺角茶餐厅", "personName": "上海市普陀区真北路988号创邑金沙谷6号楼113" },
      ],
      state2: '',
      formText: '充值（元）：',
      rechargeMoney: false,
      rechargeTime: false,
      userCardCode: '',
      deadlineDate: '',
      isActive: -1,
      email: false, // 邮箱注册
      contactemail: false,
      register: false, // 注册隐藏
      needRegister: false, // 表示需要注册
      contactRegister: false, // 联名会员需要注册
      peronList: [],
      seachType: '',
      fitstPersonMobile: '', // 为了记录第一次获取的手机号
      firstComMobile: '' // 为了记录第一次获取的联名手机号
    }
  },
  components: {
    register
  },
  created () {
    this.modify = this.$route.query.modify
    this.personId = this.$route.query.personId
    this.userCardCode = this.$route.query.code
    if (this.personId) {
      // this.form = getSessionStorage('detailInfo', {}) // 从之前的页面跳转传过来的数据
      this.getpercard()
      this.text = '修改会员卡'
    }
    // 获取所有分店
    this.initBranch()
    // 获取所有的会员卡种类类型
    // this.initTypeList()
    // 获取所有的教练即销售
    this.getallcoach()
  },
  watch: {
    // 动态设置有效期
    'form.amount': function (newVal, oldVal) {
        // if (this.form.vipCardType !== 'DATELIMIT' || this.form.startTime === '') return
        let FormatReg = /^\d+$/
        if (this.form.amount.length) {
          if (FormatReg.test(newVal)) {
            this.form.amount = newVal
          } else {
            setTimeout(() => {
              this.form.amount = oldVal
            }, 1)
          }
        }
        if (this.form.vipCardType === 'DATELIMIT' || this.form.vipCardType === 'PACKAGE') {
          this.deadline = true
          if (newVal) {
            if (!this.form.startTime) {
              let date1 = new Date()
              date1.setDate(date1.getDate() + parseInt(newVal))
              this.deadlineDate = date1
            } else {
              let date1 = new Date(this.form.startTime)
              date1.setDate(date1.getDate() + parseInt(newVal))
              this.deadlineDate = date1
            }
          } else {
            this.deadlineDate = ''
          }
        } else {
          // let date1 = new Date(this.deadlineDate)
          // date1.setDate(date1.getDate() + parseInt(newVal))
          // this.deadlineDate = date1
        }
    },
    'form.startTime': function (newVal) {
        if ((this.form.vipCardType === 'DATELIMIT' || this.form.vipCardType === 'PACKAGE') && !this.form.amount) return
        if ((this.form.vipCardType === 'TIMES' || this.form.vipCardType === 'PREPAID') && !this.form.dayLimit) return
        if (!newVal) {
          let date1 = new Date()
          if ((this.form.vipCardType === 'DATELIMIT' || this.form.vipCardType === 'PACKAGE')) {
            date1.setDate(date1.getDate() + parseInt(this.form.amount))
          } else {
            date1.setDate(date1.getDate() + parseInt(this.form.dayLimit))
          }
          this.deadlineDate = date1
          return
        }
        if (this.form.vipCardType === 'DATELIMIT' || this.form.vipCardType === 'PACKAGE') {
          if (!this.form.startTime) {
            let date1 = newVal
            date1.setDate(date1.getDate() + parseInt(this.form.amount))
            this.deadlineDate = date1
          } else {
            let date1 = new Date(this.form.startTime)
            date1.setDate(date1.getDate() + parseInt(this.form.amount))
            this.deadlineDate = date1
          }
        } else {
          if (!this.form.startTime) {
            let date1 = newVal
            date1.setDate(date1.getDate() + parseInt(this.form.dayLimit))
            this.deadlineDate = date1
          } else {
            let date1 = new Date(this.form.startTime)
            date1.setDate(date1.getDate() + parseInt(this.form.dayLimit))
            this.deadlineDate = date1
          }
        }
    },
    // 设置有效天数
    'form.dayLimit': function (newVal, oldVal) {
      let FormatReg = /^\d+$/
      if (this.form.dayLimit.length) {
        if (FormatReg.test(newVal)) {
          this.form.dayLimit = newVal
        } else {
          setTimeout(() => {
            this.form.dayLimit = oldVal
          }, 1)
        }
      }
      if (this.form.vipCardType !== 'DATELIMIT' && this.form.vipCardType !== 'PACKAGE') {
        this.deadline = true
        if (newVal) {
          if (!this.form.startTime) {
            let date1 = new Date()
            date1.setDate(date1.getDate() + parseInt(newVal))
            this.deadlineDate = date1
          } else {
            let date1 = new Date(this.form.startTime)
            date1.setDate(date1.getDate() + parseInt(newVal))
            this.deadlineDate = date1
          }
        } else {
          this.deadlineDate = ''
        }
      } else {
        let date1 = new Date(this.deadlineDate)
        date1.setDate(date1.getDate() + parseInt(newVal))
        this.deadlineDate = date1
      }
    },
    'form.vipCardType': function (newVal) {
        if ((newVal === 'DATELIMIT' || newVal === 'PACKAGE') && !this.form.startTime && !this.form.amount) {
          this.deadlineDate = new Date()
          return
        }
        if ((newVal === 'TIMES' || newVal === 'PREPAID') && !this.form.startTime && !this.form.dayLimit)  {
          this.deadlineDate = '' 
          this.deadline = false
          return
        }
        // if (!newVal) {
        //   let date1 = new Date()
        //   if ((newVal === 'DATELIMIT' || newVal === 'PACKAGE')) {
        //     date1.setDate(date1.getDate() + parseInt(this.form.amount))
        //   } else {
        //     date1.setDate(date1.getDate() + parseInt(this.form.dayLimit))
        //   }
        //   this.deadlineDate = date1
        //   return
        // }
        console.log('shuj ', newVal)
        if (newVal === 'DATELIMIT' || newVal=== 'PACKAGE') {
          if (!this.form.startTime && this.form.amount) {
            let date1 = new Date()
            date1.setDate(date1.getDate() + parseInt(this.form.amount))
            this.deadlineDate = date1
          } else if (!this.form.amount && this.form.startTime) {
            let date1 = new Date(this.form.startTime)
            this.deadlineDate = date1
          } else {
            let date1 = new Date(this.form.startTime)
            date1.setDate(date1.getDate() + parseInt(this.form.amount))
            this.deadlineDate = date1
          }
        } else {
          if (!this.form.startTime && this.form.dayLimit) {
            let date1 = new Date()
            date1.setDate(date1.getDate() + parseInt(this.form.dayLimit))
            this.deadlineDate = date1
          } else if (!this.form.dayLimit && this.form.startTime) {
            let date1 = new Date(this.form.startTime)
            date1.setDate(date1.getDate() + parseInt(this.form.dayLimit))
            this.deadlineDate = date1
          } else {
            let date1 = new Date(this.form.startTime)
            date1.setDate(date1.getDate() + parseInt(this.form.dayLimit))
            this.deadlineDate = date1
          }
        }
    },
    // 设置
    'form.personMobile': function (newVal) {
      if (!newVal.length || (this.fitstPersonMobile !=='' && newVal != this.fitstPersonMobile)) {
        this.form.personId = ''
        this.form.personName = ''
        this.needRegister = false
        this.email = false
      }
    },
    'form.combinMobile': function (newVal) {
      if (!newVal.length || (this.firstComMobile !=='' && newVal != this.firstComMobile)) {
        this.form.combinationPersonId = ''
        this.form.combinName = ''
        this.contactRegister = false
        this.contactemail = false
      }
    }
  },
  computed: {
    // 计算属性的 getter
    timePer: function () {
      // `this` 指向 vm 实例
      let res = 0
      if (this.form.vipCardType && this.form.vipCardType === 'TIMES'){
        if (this.form.actualPay && this.form.amount) {
          res = this.form.actualPay / this.form.amount
        }
      }
      return res
    }
  },
  methods: {
    // 增加私教课
    async submit (ruleForm) {
      let self = this
      this.$refs[ruleForm].validate((valid) => {
        if (valid) {
          if (!this.modify) {
            console.log('开始时间', self.form.startTime)
            if (self.form.startTime) {  // 为了number类型转换为Date类型，不然校验时会报错
              self.form.startTime = new Date(self.form.startTime).getTime()
            } else {
              self.$message({
                showClose: true,
                message: '开始时间不能为空',
                type: 'success'
              })
              return
            }
            addcardvip(self.form).then(function (res) {
              if (isApiResponseOk(res)) {
                self.$message({
                  showClose: true,
                  message: '增加成功',
                  type: 'success'
                })
                self.$router.push({name: 'memberCard'})
              }
            })
          } else {
          }
        }
      })
    },
    // 返回到私教课程页面
    goback () {
      this.$router.push({name: 'memberCard'})
    },
    handleClose(tag) {
      this.tags.splice(this.tags.indexOf(tag), 1)
    },
    // 返回输入建议
    async querySearch (number, str) {
      if (!number) {
          this.$message({
            type: "warning",
            message: "手机号/姓名/邮箱不能为空"
          })
          return
      }
      let data = {
        criteria: number
      }
      const res = await queryMember(data)
      if (isApiResponseOk (res)) {
        if (res.data && res.data.studentStubPOList) {
          let item = res.data.studentStubPOList
          if (item.length === 1) {
            item = item[0]
            if (str === 'personMobile') {
              this.form.personId = String(item.personId)
              this.form.personName = !item.personName ? item.phoneNum : !item.phoneNum ? item.email : item.personName
              this.form.newpersonMobile = !item.phoneNum ? item.email : item.phoneNum
              this.fitstPersonMobile = this.form.personMobile
            } else if (str === 'combinMobile') {
              this.form.combinationPersonId = String(item.personId)
              this.form.combinName = !item.personName ? item.phoneNum : !item.phoneNum ? item.email : item.personName
              this.form.newcombinMobile = !item.phoneNum ? item.email : item.phoneNum
              this.firstComMobile = this.form.combinMobile
            }
            this.peronList = []
            this.seachType = ''
          } else {
            this.peronList = item
            this.seachType = str
          }
          
        } else {
          // let emialReg = /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/
          let FormatReg = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/  // 手机号的验证规则
          if (str === 'personMobile') {
            if (!FormatReg.test(number)) {  
              this.email = true
              this.needRegister = false
            } else {
              this.needRegister = true
              this.email = false
            }
          } else if (str === 'combinMobile'){
            if (!FormatReg.test(number)) {  
              this.contactemail = true
              this.contactRegister = false
            } else {
              this.contactRegister = true
              this.contactemail = false
            }
          }
        }
      }
      // var results = queryString ? rest.filter(this.createFilter(queryString))
      // 调用callback 返回建议列表的数据
    },
    searchClick (item) {
      if (this.seachType === 'personMobile') {
        this.form.personId = String(item.personId)
        this.form.personName = !item.personName ? item.phoneNum : !item.phoneNum ? item.email : item.personName
        this.form.newpersonMobile = !item.phoneNum ? item.email : item.phoneNum
        this.fitstPersonMobile = this.form.personMobile
      } else if (this.seachType === 'combinMobile') {
        this.form.combinationPersonId = String(item.personId)
        this.form.combinName = !item.personName ? item.phoneNum : !item.phoneNum ? item.email : item.personName
        this.form.newcombinMobile = !item.phoneNum ? item.email : item.phoneNum
        this.firstComMobile = this.form.combinMobile
      }
      this.peronList = []
    },
    // 返回输入建议
    async querySearchName (queryString, cb) {
      var rest = []
      let data = {
        criteria: queryString
      }
      const res = await queryMember(data)
      if (isApiResponseOk (res)) {
        if (res.data && res.data.studentStubPOList) {
          rest = res.data.studentStubPOList
          rest.filter(function(item, index){
            if (!item.personName) {
              if (!item.phoneNum) {
                item.value = item.email
              } else {
                item.value = item.phoneNum
              }
            } else {
              item.value = item.personName
            }
          })
          cb(rest)
        } else {
          rest = [{value: '未搜索到结果！', uuid: '-1'}]
          cb(rest)
        }
      }
      // var results = queryString ? rest.filter(this.createFilter(queryString))
      // 调用callback 返回建议列表的数据
    }, 
    // 获取到的数据
    handleSelect (item) {
      if (item.personId) {
        this.form.personId = String(item.personId)
      } else {
        this.form.personId = ''
      }
    },
    handleSelectCom (item) {
      if (item.personId) {
        this.form.combinationPersonId = item.personId
      } else {
        this.form.combinationPersonId= ''
      }
    },
    async initBranch () {
      const res = await getStartBranch()
      if (isApiResponseOk(res)) {
        this.branchlist = res.data
      }
    },
    // 获取所有的会员卡类型
    async initTypeList (data) {
      this.cardTypelist = []
      let type = {
        cardType: data
      }
      this.form.cardTypeInfoCode = ''
      const res = await getcardtype (type)
      if (isApiResponseOk(res)) {
        if (res.data) {
          for (let item of res.data) {
            if (item.branchOfficeIds.indexOf(this.form.openCardByBranchId) > -1) {
              this.cardTypelist.push(item)
            }
          }
        }
      }
    },
    async selBranch () {
      if (this.form.vipCardType) {
        this.initTypeList(this.form.vipCardType)
      }
    },
    // 获取所有的教练
    async getallcoach () {
      const res = await getAllsale()
      if (isApiResponseOk(res)) {
        this.salelist = res.data
      }
    },
    // 会员卡类型切换
    cardType () {
      this.initTypeList(this.form.vipCardType)
      switch (this.form.vipCardType) {
        case 'DATELIMIT':
          this.formText = '充值（天）'
          this.rechargeMoney = false
          this.rechargeTime = false
          return
        case 'TIMES':
          this.formText = '充值（次）'
          this.rechargeMoney = false
          this.rechargeTime = true
          return
        case 'PREPAID':
          this.formText = '充值（元）'
          this.rechargeMoney = true
          this.rechargeTime = false
          return
        case 'PACKAGE':
          this.formText = '充值（天）'
          this.rechargeMoney = false
          this.rechargeTime = false
          return
      }
    },
    inDiv: function (data) {
      if (parseInt(data) === 3) {
        // 表示会员卡种类
        if (!this.form.openCardByBranchId) {
          this.$message({
            type: "warning",
            message: "请先选择门店"
          })
          this.$refs['cardTypeInfoCode'].blur();
          return
        } else if (!this.form.vipCardType) {
          this.$message({
            type: "warning",
            message: "请先选择会员卡类型"
          })
          this.$refs['cardTypeInfoCode'].blur();
          return
        }
      }
      this.isActive = data
    },
    outDiv: function (data) {
      this.isActive = -1
    },
    // 注册会员
    registPeron (str) {
      this.register = true
    },
    close () {
      this.register = false
      // this.initCardList(this.para)
    },
    login (data) {
      if (this.needRegister) {
        this.needRegister = false
        this.form.personMobile = data.accountMobile
        this.form.personId = data.personId
        this.form.personName = data.personName ? data.personName : data.accountMobile ? data.accountMobile : data.accountEmail
      } else if (this.contactRegister) {
        this.contactRegister = false
        this.form.combinMobile = data.accountMobile
        this.form.combinationPersonId = data.personId
        this.form.combinName = data.personName ? data.personName : data.accountMobile ? data.accountMobile : data.accountEmail
      }
    }
  }
}
</script>
<style lang="sass">
.leakind
    .el-form
        height: 6.3rem;
        .search
          .el-form-item__content
            display: flex;
            align-items: center;
            .el-input
              width: 80%;
              .el-input__inner
                position: relative;
                text-align: left;
                &:after
                  content: '|';
                  position: absolute;
          .sea
            width: .72rem;
            text-align: center;
            position: relative;
            font-size: .16rem;
            color: #20C6BA;
            cursor: pointer;
            &:before
                content: '|';
                position: absolute;
                left: -0.1rem;
                color: #EAECEF;
        .per
          font-family: PingFangSC-Regular;
          font-size: .14rem;
          color: #C0C4CC;
          letter-spacing: 0;
          line-height: .14rem;
          margin-top: .08rem;
          margin-bottom: .16rem;
          display: flex;
          .noperson
            color: #F44A6C;
            margin-right: .2rem;
            img
              margin-right: .08rem;
          .regist
            span
              color: #20C6BA;
              cursor: pointer;
        .person
          margin-bottom: 0px !important;
    .btn
        display: flex;
        justify-content: center;
        display: inline-block;
        text-align: center;
        .submit
            background: #20C6BA;
            border-radius: .04rem;
            color: #fff;
            font-size: .14rem;
            width: 1.2rem;
            height: .44rem;
            line-height: .44rem;
            color: #fff;
            text-align: center;
            margin-top: .3rem;
            cursor: pointer;
    .top
        margin-bottom: .2rem;
        display: flex;
        align-items: center;
        .fundInfo
            font-size: .17rem;
            color: #8597A1;
            cursor: pointer;
        .branch
            font-size: .21rem;
            color: #263238;
        img
            width: .16rem;
            margin-left: .12rem;
            margin-right: .2rem;
    .text
        font-size: .14rem;
        color: #C0C4CC;
        text-align: right;
    .balance
        font-size: .24rem;
        color: #263238;
        text-align: right;
    .dead
        font-size: .20rem;
        margin-top: .05rem;
    .tips
        font-size: .12rem;
        margin-bottom: .2rem;
        color: #C0C4CC;
        text-align: right;
        margin-top: -0.1rem;
    .timePer
        font-size: .11rem;
        font-family: PingFangSC-Regular;
        color: #8597A1;
        float: right;
        margin-top: -.14rem;
        margin-bottom: 0.2rem;
.leakind
  .search
    position: relative;
    .searchList
      position: absolute;
      top: 0.55rem;
      left: 0;
      margin: 0;
      background: #fff;
      width: 100%;
      border: 1px solid #E9ECEF;
      border-top: 0;
      padding: 0;
      z-index: 11;
      max-height: 3rem;
      overflow-y: auto;
      li
        list-style: none；
      .searchItem
        line-height: 0.4rem;
        padding: 0 0.1rem;
        box-sizing: border-box;
        cursor: pointer;
        &:hover
          background: #E9ECEF;
</style>



