<template>
    <div class='register'>
        <el-dialog title='用户注册' :visible.sync="dialogFormVisible" :before-close="handleClose">
            <el-form :model="form" ref='form'>
                <el-form-item label="手机号:" class='reForm'>
                    <el-input v-model="form.mobile" autocomplete="off" :maxlength="11"></el-input>
                </el-form-item>
                <el-form-item label="手机验证码：" class='reForm search'>
                     <el-input v-model="form.password" autocomplete="off"></el-input>
                     <el-button @click="querySearch()"  :class="{sea: true, unclick : hasGet}">{{text}}</el-button>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="submit()">注册</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
import {sendCode, coachLogin, fitStudent} from '../services/service.js'
import {isApiResponseOk} from '../utils/com.js'
export default {
    data () {
        return {
            form: {
                mobile: '',
                password: '',
            },
            text: '验证码',
            time: 0,
            hasGet: false,
        }
    },
    props: ['dialogFormVisible'],
    methods: {
        // 获取手机号验证码
        async querySearch () {
            if (this.form.mobile === '') {
                this.$message({
                    message: '手机号不能为空',
                    type: 'warning'
                })
                return
            } else {
                let FormatReg = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/
                if (this.form.length) {
                    if (!FormatReg.test(this.form.mobile)) {
                        this.$message({
                            message: '请填写正确的手机格式',
                            type: 'warning'
                        })
                        return
                    }
                }
            }
            let data = {
                phoneNumber: this.form.mobile,
                use: '1'
            }
            const res = await sendCode (data)
            if (isApiResponseOk(res)) {
                this.hasGet = true
                this.run()
                this.$message({
                    showClose: true,
                    message: '验证码发送成功',
                    type: 'success'
                })
            }
        },
        run () {
            this.time = 60
            this.timer()
        },
        timer () {
            if (this.time > 0) {
                this.text = '( ' + this.time + ' s)'
                this.time--
                setTimeout(this.timer, 1000)
            } else {
                this.hasGet = false
                this.text = '验证码'
            }
        },
        handleClose () {
            let self = this
            this.$confirm('确认关闭？', {type: 'warning'})
                .then(_ => {
                    self.resetForm('form')
                })
                .catch(_ => {})
        },
        resetForm(formName) {
            this.form.mobile = ''
            this.form.password = ''
            this.$refs[formName].resetFields();
            this.$emit('close', close);
        },
        async submit () {
            let self = this
            if (self.form.mobile === '') {
                self.$message({
                    message: '手机号不能为空',
                    type: 'warning'
                })
                return
            }
            if (self.form.password === '') {
                self.$message({
                    message: '验证码不能为空',
                    type: 'warning'
                })
                return
            }
            let data = {
                verifyCode: this.form.password,
                phoneNumber: this.form.mobile
            }
            const res = await coachLogin(data)
            if (isApiResponseOk(res)) {
                this.fitStudent()
            }
        },
        async fitStudent () {
            const res = await fitStudent()
            if (isApiResponseOk(res)) {
                this.$message({
                    showClose: true,
                    message: '注册成功',
                    type: 'success'
                }) 
                this.resetForm('form')
                this.$emit('close', close)
                this.$emit('login', res.data)
            }
        }
    }
}
</script>
<style lang="scss" scoped>
.register{
    .el-dialog__wrapper .el-dialog{
        width: 21.9% !important;
        .el-dialog__body{
            padding: 30px 0px;
        }
    }
    .el-form{
        .reForm{
            width: 3.2rem !important;
            height: .4rem !important;
        }
        .el-input{
           height: .4rem !important;
           line-height: .4rem !important; 
           .el-input__inner{
               text-align: left;
           }
        }
        .search .sea{
            cursor: pointer;
            font-size: .14rem;
            border: none;
            background: #FBFBFC;
        }
        .unclick{
            cursor: default;
            pointer-events: none;
        }
    }
}
</style>





